import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import preivew_logo from 'image/lich.png';
import preivew_logo_1 from 'image/am.png';
import preivew_logo_2 from 'image/pa.png';
import preview from '../cfg/preview.json';
import 'styles/index.scss';

class App extends Component {
  constructor() {
    super();
    this.state = { tabIndex: 0 };
  }
  render() {
    console.log(preivew_logo);
    return (
      <div className="App">
        <Tabs className="container">
          <TabList
            className="list"
            selectedindex={this.state.tabIndex}
            onSelect={tabIndex => this.setState({ tabIndex })}
          >
            <Tab className="link heroes">
              <span>Герои</span>
            </Tab>
            <Tab className="link items">
              <span>Предметы</span>
            </Tab>
            <Tab className="link creeps">
              <span>Крипы</span>
            </Tab>
          </TabList>
          <TabPanel className="preview">
            <div className="preview-wrap">
              <div className="preview-text">
                <h1>Герои</h1>
                <p>
                  В данном разделе вы можете ознакомиться и узнать о героях Dota 2, а именно:
                </p>
                <span>характеристика;</span>
                <span>описание героя;</span>
                <span>описание способностей;</span>
                <span>история.</span>
                <a href="#" className="button">Перейти</a>
              </div>
              <div className="preview-image">
                <img src="image/lich.png" alt=""/>
              </div>
            </div>
          </TabPanel>
          <TabPanel className="preview">
            <div className="preview-wrap preview-wrap-reverse">
              <div className="preview-text">
                <h1>Предметы</h1>
              </div>
              <div className="preview-image preview-image-1">
                <img src={preivew_logo_1} alt=""/>
              </div>
            </div>
          </TabPanel>
          <TabPanel className="preview">
            <div className="preview-wrap">
              <div className="preview-text">
                <h1>Крипы</h1>
              </div>
              <div className="preview-image">
                <img src={preivew_logo_2} alt=""/>
              </div>
            </div>
          </TabPanel>
        </Tabs>
      </div>
    );
  }
}

export default App;



